<template>
  <div class="flex-column vfull">
    <div>
      <div class="flex-row topbar">
        <button @click="choosePassage"><i class="fa fa-book"></i> Passage</button>
        <p v-if="!isLoaded">Loading...</p>
        <div class="flex-one"></div>
        <div class="effects">
          <div v-for="effect in effects" class="effect" :class="effectClasses(effect)" @click="activeEffect = effect"><span>{{effect.substring(0, 1).toUpperCase()}}</span></div>
        </div>
      </div>
      <div class="settings">
        <input type="number" name="tempo" v-model="tempo" />
      </div>
    </div>

    <div class="flex-one scrolly">
      <div class="words">
        <h3>{{book}} {{chapter}}</h3>
        <div v-for="(word, index) in words" class="word" :class="wordClasses(word, index)" @click="wordPressed(word, index, $event.target)">{{word.text}} </div>
      </div>
    </div>

    <div class="controls-container">
      <div class="controls">
        <button @click="toBeginning"><i class="fa fa-step-backward"></i></button>
        <button v-if="isLoaded && !isPlaying" @click="play"><i class="fa fa-play fa-2x"></i></button>
        <button v-if="isPlaying && !isPaused" @click="pause"><i class="fa fa-pause fa-2x"></i></button>
        <button @click="nextWord"><i class="fa fa-step-forward"></i></button>
      </div>
    </div>
  </div>
</template>

<script>
import bibleLoader from '../../static/js/helpers/bibleloader'
import midi from '../../static/js/helpers/midiword'

export default {
  name: 'home',
  data () {
    return {
      tempo: 120,
      verses: undefined,
      words: [],
      book: undefined,
      chapter: undefined,
      effects: ['tonic', 'fifth', 'glissando', 'strings'],
      activeEffect: 'glissando',
      currentWord: -1,
      isLoaded: false,
      isPlaying: false,
      isPaused: false
    }
  },
  watch: {
    tempo (value) {
      midi.tempo = parseInt(value)
    }
  },
  methods: {
    choosePassage () {
      this.$router.push('/passage')
    },
    effectClasses (effect) {
      var classes = [effect]
      if (this.activeEffect === effect) {
        classes.push('current')
      }
      return classes
    },
    wordClasses (word, index) {
      var classes = []
      if (index === this.currentWord) {
        classes.push('current')
      }
      this.effects.forEach(effect => {
        if (word[effect]) {
          classes.push(effect)
        }
      })
      return classes
    },
    wordPressed (word, index, element) {
      word[this.activeEffect] = !word[this.activeEffect]
      element.classList.toggle(this.activeEffect)
    },
    play () {
      this.nextWord()
      if (this.isPaused) {
        this.isPaused = false
        return
      }
      this.isPlaying = true
      var wordEntry = this.words[this.currentWord]
      var word = wordEntry.text
      var t = midi.msPerBeat() * (word.length / 2 > 4 ? 4 : (word.length / 2 > 2 ? 2 : 1))
      setTimeout(this.play, t)
      if (wordEntry.tonic) {
        midi.playTonic(0, wordEntry.glissando)
      }
      if (wordEntry.fifth) {
        midi.playFifth(0, wordEntry.glissando)
      }
      if (wordEntry.glissando && !(wordEntry.tonic || wordEntry.fifth)) {
        midi.glissando(word)
      }
      if (wordEntry.strings) {
        midi.playWord(word.substring(0, 1), 1)
        midi.playWord(word)
      }
      var hasNoEffects = this.effects.find(effect => wordEntry[effect] === true) === undefined
      if (hasNoEffects) {
        midi.playWord(word)
      }
    },
    pause () {
      this.isPaused = true
      this.isPlaying = false
    },
    nextWord () {
      this.currentWord = this.currentWord === this.words.length ? 0 : this.currentWord + 1
    },
    toBeginning () {
      this.currentWord = 0
    }
  },
  mounted () {
    const book = this.$route.query.book
    const chapter = this.$route.query.chapter
    if (book && chapter) {
      const self = this
      self.isLoaded = false
      bibleLoader.load(book, chapter, 'esv', verses => {
        self.book = book
        self.chapter = chapter
        self.verses = verses
        self.words = []
        verses.forEach(v => {
          self.words = self.words.concat(v.text.split(' ')
          .map(w => {
            return {text: w}
          }))
        })
        self.isLoaded = true
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../static/less/flex';
@import '../../static/less/common';

.topbar {
  border-bottom: solid 1px @color-blue;
  padding: 10px;
  align-items: center;
  button {
    background-color: transparent;
    border: solid 1px @color-blue;
    cursor: pointer;
    border-radius: 4px;
    padding: 10px;
    color: white;
    font-size: 14px;
    outline: none;
    &:hover {
      text-decoration: underline;
    }
  }
}
.settings {
  padding: 10px;
  border-bottom: solid 1px @color-blue;
  input {
    font-size: 16px;
    border-radius: 4px;
    border: solid 1px @color-blue;
    background-color: transparent;
    color: white;
    padding: 8px;
  }
}
.controls-container {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: transparent;
  background: linear-gradient(transparent, black);
  .controls {
    display: inline-block;
    margin-left: 50%;
    transform: translateX(-50%);
    background-color: @color-back;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    min-width: 100px;
    box-shadow: 0px -2px 5px black;
  }
  button {
    background-color: transparent;
    border: none;
    padding: 15px;
    color: white;
    font-size: 14px;
    outline: none;
    &:hover {
      color: @color-blue;
    }
  }
}
.effects {
  display: flex;
  flex-direction: row;
  align-items: center;
  .effect {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    margin-right: 10px;
    border-radius: 25px;
    border: solid 2px transparent;
    color: white;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    &.current {
      border: solid 2px #7cf;
      font-size: 22px;
      text-shadow: 0px 0px 3px black;
      box-shadow: 0px 0px 9px 1px #ddddff;
    }
    &.tonic {
      background-color: @color-tonic;
    }
    &.fifth {
      background-color: @color-fifth;
    }
    &.glissando {
      background-color: @color-blue;
    }
    &.strings {
      background-color: #ff9911;
    }
  }
}
.words {
  line-height: 28px;
  font-size: 17px;
  padding: 10px;
  padding-bottom: 100px;
  h3 {
    text-align: center;
    color: #bbb;
  }
  .word {
    display: inline;
    transition: color 0.2s;
    border: solid 2px transparent;
    text-shadow: 0px 0px 3px black;
    cursor: pointer;
    &.current {
      color: #ffaa00;
    }
    &.tonic {
      background-color: @color-tonic;
    }
    &.fifth {
      background-color: @color-fifth;
    }
    &.glissando {
      border-bottom: solid 2px @color-blue;
    }
    &.strings {
      border-left: solid 2px #ff9911;
    }
  }
  .reference {
    color: #999;
    vertical-align: super;
    font-size: 11px;
  }
}
</style>



